  padding: 0; }

@viewport {
  width: device-width; }

html {
  background-color: whitesmoke;
  box-sizing: border-box;
  height: 100%; }

* {
  box-sizing: border-box; }
  *:before, *:after {
    box-sizing: inherit; }

body {
  -webkit-font-smoothing: antialiased;
  background: whitesmoke;
  color: #404040;
  font-family: "Open sans" san serif;
  font-size: calc(18px + (22 - 18) * (100vw - 320px)/(1330 - 320));
  min-height: 100%;
  overflow-x: hidden;
  text-rendering: optimizeLegibility; }

