.menu {
  width: 100vw;
  min-height: 100vh;
  position: fixed;
  top: 0;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f19e22;
  color: whitesmoke;
  transform: translate(0, -100vh);
  transition: all 0.5s ease-out;
  transition-duration: 0.5s; }
  .menu-nav {
    list-style: none;
    margin: 0;
    padding: 0;
    height: 100vh;
    max-width: 700px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-flow: column wrap;
    font-size: 1.8rem; }
    .menu-nav__item {
      margin-bottom: 1rem;
      padding-left: 1rem;
      border-left: 2px solid #ba4c20;
      transition: all 0.5s ease-out; }
      @media screen and (min-width: 960px) {
