        transition: all 0.5s ease-out; }
  @media screen and (min-width: 960px) {
    .menu {
      z-index: 0;
      width: auto;
      height: 100%;
      min-height: auto;
      position: static;
      display: inline;
      background: transparent;
      color: #1a1a1a;
      transform: translate(0, 0); }
      .menu-nav {
        height: auto;
        display: inline; }
        .menu-nav__item {
          height: 100%;
          margin: 0 1rem;
          border-left: 0;
          padding: 1rem;
          display: inline;
          font-size: 1.4rem;
          transition: all 0.5s ease-out; }
          .menu-nav__item:hover {
            background: #ba4c20; }
            .menu-nav__item:hover > * {
              color: white;
              padding-left: 0; } }

