/*--------------------------------------------------------
Rotate menu button into X 
--------------------------------------------------------*/
.menu-btn.isClosed {
  transform: rotate(180deg); }
  .menu-btn.isClosed .menu-btn__line {
    background: white;
    transition: all 0.5s ease-out; }
    .menu-btn.isClosed .menu-btn__line:nth-child(1) {
      transform: rotate(45deg) translate(5px, 7px); }
    .menu-btn.isClosed .menu-btn__line:nth-child(2) {
      opacity: 0; }
    .menu-btn.isClosed .menu-btn__line:nth-child(3) {
      transform: rotate(-45deg) translate(8px, -9px); }

/*--------------------------------------------------------
Menu dropdown 
--------------------------------------------------------*/
.menu.isShown {
  transform: translate(0, 0);
  transition: all 0.5s ease-out;
  transition-duration: 0.5s; }

.isClicked {
  border: 2px solid #ba4c20;
  background: transparent;
  color: #ba4c20; }
